#preloader {
  position: relative;
}

#preloader,
.loader,
.loader::before {
  height: 4px;
  background-color: #DDDDDD;
}

.loader {
  width: 100%;
  overflow: hidden;
  margin-bottom: 7px;
  position: absolute;
}

.loader::before {
  content: "";
  left: -200px;
  width: 200px;
  display: block;
  position: absolute;
  background-color: #2980B9;
  -webkit-animation: loading 2s linear infinite;
          animation: loading 2s linear infinite;
}

@-webkit-keyframes loading {
  from {
    left: -200px;
    width: 30%;
  }

  50% {
    width: 30%;
  }

  70% {
    width: 70%;
  }

  80% {
    left: 50%;
  }

  95% {
    left: 120%;
  }

  to {
    left: 100%;
  }
}

@keyframes loading {
  from {
    left: -200px;
    width: 30%;
  }

  50% {
    width: 30%;
  }

  70% {
    width: 70%;
  }

  80% {
    left: 50%;
  }

  95% {
    left: 120%;
  }

  to {
    left: 100%;
  }
}



