.screen-header {
  background-color: #222222;
  height: 150px;
  padding: 20px;
  color: white;
}

.screen-logo {
  -webkit-animation: screen-logo-spin infinite 20s linear;
          animation: screen-logo-spin infinite 20s linear;
  height: 80px;
}

@-webkit-keyframes screen-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes screen-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

